let libraryPack;
let typesPack;

export const setLibraryPack = (pack) => {
    libraryPack = pack;
}

export const getLibraryPack = () => {
    return libraryPack;
}

export const setTypesPack = (pack) => {
    typesPack = pack;
}

export const getTypesPack = () => {
    return typesPack;
}

